@import url("https://fonts.googleapis.com/css2?family=Marhey:wght@300;400;600&family=Raleway:ital,wght@0,200;0,300;0,400;0,500;0,600;1,100&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,200;0,300;0,400;0,500;0,600;1,100&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
body{
  background-color: #4d4c7d;
  
}
.random_container {
  max-width: 480px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 35vh;
  margin: 2rem auto;
  background-color: #363062;
  border-radius: 1rem;
  padding: 2rem;
  box-shadow: 1px 30px 10px -8px rgba(0, 0, 0, 0.1);
}
.random_container button {
  background-color: #4d4c7d;
  position: fixed;
  border-radius: 1rem;
  padding: 0.5rem 1rem;
  border: none;
  margin-top: 5rem;
  color: #f99417;
  font-size: 14px;
  outline: none;
  text-shadow: 0 13px 10px rgba(0, 0, 0, 0.2);
}
.random_container p {
  margin-bottom: 3rem;
  color: #f99417;
  font-size: 13px;
  line-height: 1.6;
  overflow: hidden;
  padding: 0.3rem;
  font-weight: 300;
  font-family: "Marhey", sans-serif;
}
.text-heading {
  margin-top: 3rem;
  color: #f99417;
  font-size: 40px;
  font-weight: 600;
  font-family: "Raleway", sans-serif;
  text-align: center;
  padding-bottom: 1rem;
  text-shadow: 0 15px 10px rgba(0, 0, 0, 0.2);
}
.random_container:hover {
  color: #eee;
  transform: scale(1.03);
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
}
button:hover{
  color: #eee;
  transform: scale(1.03);
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
}
h4{
  position: absolute;
  bottom: 2.5rem;
  right: 2.5rem;
  color: #f99417;
  font-family: "Marhey", sans-serif;
  font-weight: 500;
}